import { Component, OnInit } from '@angular/core';
import { Camera, CameraResultType, CameraSource, Photo } from '@capacitor/camera';
import { ModalController } from '@ionic/angular';
import { NgxSpinnerService } from 'ngx-spinner';
import { BehaviorSubject, ReplaySubject } from 'rxjs';
import { AppConstants, ProfileConstants } from 'src/app/app.constants';
import { EditProfileModalComponent } from 'src/shared/components/edit-profile-modal/edit-profile-modal.component';
import { IShopper } from 'src/shared/models/type';
import { UserPhoto } from 'src/shared/models/wardrobe.model';
import { DataService } from 'src/shared/services/data.service';
import { ToasterService } from 'src/shared/services/toaster.service';
import { AppStateService } from 'src/shared/state/app.state.service';
import { ImageCropModalComponent } from '../../wardrobe/image-crop-modal/image-crop-modal.component';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.scss'],
})
export class EditProfileComponent implements OnInit {
  currentUser$: BehaviorSubject<IShopper>;
  genders = ProfileConstants.genders;
  public photos: UserPhoto[] = [
    {
      filepath: '',
      webviewPath: '',
    },
    {
      filepath: '',
      webviewPath: '',
    },
  ];
  photosbase64: string;

  constructor(
    private readonly modalController: ModalController,
    private readonly dataService: DataService,
    private readonly http: HttpClient,
    private toasterService: ToasterService,
    private appState: AppStateService,
    private readonly spinner: NgxSpinnerService
  ) { }

  ngOnInit() {
    this.currentUser$ = this.appState.getUserInfos();
    //this.dataService.getUserData().then((user) => this.currentUser = user);
  }

  ionViewDidEnter() {
    this.spinner.hide('appSpinner')
  }
  /*ionViewWillEnter() {
    StatusBar.setBackgroundColor({ color: '#FFDE59' })
  }

  ionViewWillLeave() {
    StatusBar.setBackgroundColor({ color: '#FFFFFF' })
  }*/

  async onOpenModal(title: string, value: any, key: string, userID: string) {
    const modal = await this.modalController.create({
      component: EditProfileModalComponent,
      componentProps: {
        data: { title, value, key, userID },
        backdropDismiss: false,
      },
    });
    await modal.present();
    const result = await modal.onWillDismiss();

    if (result.data) {
      //this.currentUser = result.data;
    }
  }

  async onUpdateAvatar() {
    // Take a photo
    const capturedPhoto = await Camera.getPhoto({
      resultType: CameraResultType.Uri,
      source: CameraSource.Prompt,
      promptLabelPhoto: "Depuis votre galerie",
      promptLabelPicture: "Prendre une photo",
      quality: 100,
      height: 1000,
      width: 1000,
    }).then(photo => {
      this.readAsBase64(photo).then(base64 => {
        this.show(base64).then(result => {
          //console.log(this.calc_image_size(result));
          if (result.includes('data:image/png;base64,')) {
            const plop = result.replace('data:image/png;base64,', '');
            const shopper = {
              avatar_base64: plop,
            };
            this.http
              .patch(
                `${AppConstants.shopperUrl}/${this.currentUser$.getValue().id}`,
                { shopper },
                { observe: 'response' }
              )
              .subscribe((response: any) => {
                this.dataService.setData("user", response.body.data);
                this.appState.setUserInfos(response.body.data)
                //this.currentUser = response.body.data;
                this.toasterService.presentSuccessToast(`MESSAGE.PROFILE.EDIT_${response.status}`);
              }),
              error => {
                console.log(error);
                this.toasterService.presentErrorToast('SHARED.ERROR');
              };
          }
        });
      });
    });
  }
  private async readAsBase64(photo: Photo) {
    // "hybrid" will detect Cordova or Capacitor
    /* if (this.platform.is('hybrid')) {
       // Read the file into base64 format
       const file = await Filesystem.readFile({
         path: photo.path
       });
 
       return file.data;
     }
     else {*/
    // Fetch the photo, read as a blob, then convert to base64 format
    const response = await fetch(photo.webPath);
    const blob = await response.blob();

    return (await this.convertBlobToBase64(blob)) as string;

    // }
  }

  convertBlobToBase64 = (blob: Blob) =>
    new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onerror = reject;
      reader.onload = () => {
        resolve(reader.result);
      };
      reader.readAsDataURL(blob);
    });

  async show(imageBase64: string): Promise<string | null> {
    const modal: HTMLIonModalElement = await this.modalController.create({
      component: ImageCropModalComponent,
      backdropDismiss: false,
      componentProps: {
        imageBase64,
        aspectRatio: 1 / 1,
      },
    });

    await modal.present();

    const result = await modal.onWillDismiss();

    if (result.data && result.data.croppedImageBase64) {
      return result.data.croppedImageBase64;
    }

    return null;
  }
}
