import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, combineLatest, Subscription } from 'rxjs';
import { ProfileConstants, WardrobeCategory } from 'src/app/app.constants';
import { IArticle, IShopper, IStyle } from 'src/shared/models/type';
import { WardrobeStateService } from 'src/shared/state/wardrobe-state.service';
import { SubscriberComponent } from 'src/shared/utils/subscriber-component';
import { FirebaseAnalytics } from '@capacitor-community/firebase-analytics';
import { DataService } from 'src/shared/services/data.service';
import { AppStateService } from 'src/shared/state/app.state.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage extends SubscriberComponent {
  currentUser$: BehaviorSubject<IShopper>;
  genders = ProfileConstants.genders;
  subscription$: Subscription;
  allArticles: IArticle[];
  styles$: BehaviorSubject<IStyle[]>;

  constructor(
    private readonly router: Router,
    private readonly wardrobeState: WardrobeStateService,
    private readonly appState: AppStateService
  ) {
    super();
  }

  ionViewWillEnter() {
    FirebaseAnalytics.setScreenName({
      screenName: "ProfileScreen",
      nameOverride: "ProfileScreen",
    });
    FirebaseAnalytics.logEvent({
      name: "profile_page_entered",
      params: {}
    })
    this.currentUser$ = this.appState.getUserInfos()
    this.styles$ = this.wardrobeState.getAllStyles();
    this.subscription$ = this.addSubscription(
      combineLatest([this.wardrobeState.extractArticlesByCategoryId(WardrobeCategory.CLOTHES),
      this.wardrobeState.extractArticlesByCategoryId(WardrobeCategory.SHOES),
      this.wardrobeState.extractArticlesByCategoryId(WardrobeCategory.ACCESSORIES)]
      ).subscribe((categoryValues: IArticle[][]) => {
        this.allArticles = categoryValues.reduce((acc, val) => acc.concat([...val]), []);
      })
    );
  }

  ionViewWillLeave() {
    this.removeAllSubscriptions()
  }

  onGoToParameters(): void {

    this.router.navigate(['/main/profile/profile-params']);
  }

  onEditProfile(): void {
    this.router.navigate(['/main/profile/edit-profile']);
  }

}
